import React from 'react';
import ReactDOM from 'react-dom';
import { styler, tween, easing } from 'popmotion'
import './index.css';

function Path(props) {
    if (props.colors) {
        const fillColor = props.colors[RandomInt(0, props.colors.length-1)];
        return <path fill={fillColor} className={props.className} d={props.d}/>;
    } else {
        return <path className={props.className} d={props.d}/>;
    }
}

class Ship extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            right: null, // 1 = going right
            speed: null, // speed of ship (pixel per second)
            lowX: null, // where x ship begin
            highX: null, // where ship stops
            oRight: null
        };
    }
    ship_1 = {
        template: (
            <React.Fragment>
                <Path className={'wave'} d="m 8.7571463,21.070331 c 0,0 -14.7256224,0.1197 -5.9860296,2.8733 8.7395933,2.7536 33.4020173,1.6761 44.1768533,0.2394 10.774836,-1.4366 3.950772,-3.4718 -17.000311,-3.3521" />
                <Path colors={['#ff6928', '#46367f', 'red']} className={'right'} d="m 9.4754659,18.795631 h 8.7395931 v -16.5213995 z"/>
                <Path colors={['#ff6928', 'yellow', 'red']} className={'left'} d="m 31.197345,18.795631 h -9.562894 v -18.07779951 z"/>
                <Path colors={['#222', '#293237']} className={'middle'} d="m 19.172823,-4.6851109e-4 v 18.79609951109 h 1.436653 v -17.3593995 z" />
                <Path colors={['black', '#222222']} className={'base'} d="m 7.3204929,19.633731 h 26.0990631 c 0,0 0.838049,2.993 -12.09176,3.5916 -12.9298095,0.5986 -11.253725,0.1196 -14.0073031,-3.5916 z"/>
            </React.Fragment>
        ),
        speed: 15,
        oRight: 0,
    };
    ship_2 = {
        template: (
            <React.Fragment>
                <Path className={'wave'} d="m 43.242853,8.2310534 c 0,0 14.725622,0.1196584 5.986029,2.8732926 -8.739592,2.753563 -33.402016,1.676084 -44.1768526,0.23943 -10.7748362,-1.4366389 -3.9507718,-3.4718824 17.0003106,-3.3521672" />
                <Path colors={['#E3E4E6']} className={'body'} d="m 23.580064,1.8288919e-6 -1.08398,0.6265388711081 1.605169,1.6051829 h -1.810334 l 1.325185,1.3251851 h 2.866593 c 0,0 1.674493,1.3225733 1.674493,3.2380733 0,0.2394446 -6.941934,0 -6.941934,0 l -3.113333,3.831369 h 22.747009 l 4.787827,-4.7905952 h -5.26744 l -3.171544,-3.1715443 h -10.953501 z m 6.215567,3.7426400711081 h 1.142205 v 1.0590409 h -1.142205 z m 1.64954,0 h 1.144974 v 1.0590409 h -1.144974 z m 1.652323,0 h 4.316517 v 1.0590409 h -4.316517 z"/>
            </React.Fragment>
        ),
        speed: 30,
        oRight: 1, // ship orientation
    };
    ships = [this.ship_1, this.ship_2];
    ship = this.ships[Math.floor(Math.random()*this.ships.length)];

    movement(componentRoot){
        let {right, speed, oRight, lowX, highX} = this.state;
        let ship = styler(componentRoot);
        ship.set({
            y: Randomfloat(this.props.yRange[0], this.props.yRange[1]),
            x: right ? lowX : highX,
            scaleY: this.props.scale,
            scaleX: (right === oRight) ? this.props.scale : -this.props.scale
        });
        let changeDirection = (right) => {
            ship.set({
                scaleX: (right === oRight) ? this.props.scale : -this.props.scale
            });
            this.setState({
                right: right
            });
        };
        const shipAnimate = (fromX, toX, speed) => tween({
            from: { x: fromX },
            to: { x: toX},
            duration: (Math.abs(fromX-toX)/speed)*1000,
            ease: easing.linear,
        }).start({
            update: (x) => ship.set(x),
            complete: () => {
                if(fromX < toX) {
                    changeDirection(0);
                } else {
                    changeDirection(1);
                }
                setTimeout(function() {
                    shipAnimate(toX, fromX, speed)
                }, RandomInt(10000, 20000));
            }
        });
        if(right) {
            setTimeout(function() {
                shipAnimate(lowX, highX, speed)
            }, RandomInt(0, 10000));
        } else {
            setTimeout(function() {
                shipAnimate(highX, lowX, speed)
            }, RandomInt(0, 5000));
        }
    }
    // TODO: prevent parallel same type ship
    componentWillMount(){
        this.setState({
            oRight: this.ship.oRight,
            right: RandomInt(0, 1),
            speed: this.ship.speed,
            lowX: -50,
            highX: 800,
        });
    }

    componentDidMount(){
        this.movement(ReactDOM.findDOMNode(this));
    }
    componentWillUnmount() {
    }
    //style={{WebkitTransform: `translate(${x}px, ${y}px) rotateY(${rotate}deg) scale(${this.props.scale})`}}>
    //style={{WebkitTransform: `translateX(${destX}px)`, WebkitTransition: `transform ${speed}s linear`}}
    render(){
        let {right} = this.state;
        const destination = right ? 'right' : 'left';
        const classes = 'ship ' + destination;
        return(
            <g className={classes}>
                {this.ship.template}
            </g>
        );
    }
}

